import React, { useState } from 'react';
import LayoutPage from '../Layout/layout';
import firebase from '../firebase';
import { useHistory } from 'react-router-dom';
const AddTest = () => {
    const [name, setName] = useState('');
    const [pass, setPass] = useState('');
    const [num, setNum] = useState(0);
    const [type, setType] = useState('');
    const [dateStart, setDateStart] = useState('');
    const [hStart, setHStart] = useState('');
    const [mStart, setMStart] = useState('');
    const [dateFinsh, setDateFinish] = useState('');
    const [hFinish, setHFinish] = useState('');
    const [mFinish, setMFinish] = useState();

    const history = useHistory();
    const SubmitTest = () => {
        const test = firebase.database().ref('test');
        let data = {
            baithi: name,
            matkhau: pass,
            socauhoi: num,
            ngaybatdau: dateStart,
            tgbatdau: hStart+":"+mStart,
            ngayketthuc: dateFinsh,
            tgketthuc: hFinish+":"+mFinish,
            loai: type
        }
        test.push(data);
        history.push('/test');
    }
    return(
        <LayoutPage>
            <h3>Thêm bài thi</h3>
            <div className="row">
                <div className="col-sm-12">
                <div className="form-group">
  <label htmlFor="usr">Tên bài thi:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setName(e.target.value)}/>
</div>
<div className="form-group">
  <label htmlFor="usr">Mật khẩu bài thi:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setPass(e.target.value)}/>
</div>
<div className="form-group">
  <label htmlFor="usr">Số câu hỏi:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setNum(e.target.value)}/>
</div>
<div className="form-group">
  <label htmlFor="usr">Hình thức thi:</label>
  <select className="form-control" onChange={(e) => setType(e.target.value)}>
      <option>Chọn</option>
      <option value="TN">Trắc nghiệm</option>
      <option value="TL">Tự luận</option>
  </select>
</div>
<h4>Thời gian thi</h4>
<div className="row">
    <div className="col-sm-6">
        <h5>Bắt đầu</h5>
    <div className="form-group">
  <label htmlFor="usr">Ngày:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setDateStart(e.target.value)}/>
</div>
<div className="row">
    <div className="col-sm-6">
    <div className="form-group">
  <label htmlFor="usr">Giờ:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setHStart(e.target.value)}/>
</div>
    </div>
    <div className="col-sm-6">
    <div className="form-group">
  <label htmlFor="usr">Phút:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setMStart(e.target.value)}/>
</div>
    </div>
</div>
    </div>

    <div className="col-sm-6">
        <h5>Kết thúc</h5>
    <div className="form-group">
  <label htmlFor="usr">Ngày:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setDateFinish(e.target.value)}/>
</div>
<div className="row">
    <div className="col-sm-6">
    <div className="form-group">
  <label htmlFor="usr">Giờ:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setHFinish(e.target.value)}/>
</div>
    </div>
    <div className="col-sm-6">
    <div className="form-group">
  <label htmlFor="usr">Phút:</label>
  <input type="text" className="form-control" id="usr" required onChange={(e) => setMFinish(e.target.value)}/>
</div>
    </div>
    </div>
</div>
</div>
<button className="btn btn-primary btn-block" onClick={SubmitTest}>Thêm bài thi</button>
                </div>
            </div>
        </LayoutPage>
    )
}
export default AddTest;