import React from 'react';

const HeaderComponent = () => {
    return(
        <nav className="navbar navbar-default navbar-static-top">
  <div className="container-fluid">
    {/* Brand and toggle get grouped for better mobile display */}
    <div className="navbar-header">
      <button type="button" className="navbar-toggle navbar-toggle-sidebar collapsed">
        MENU
      </button>
      <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1">
        <span className="sr-only">Toggle navigation</span>
        <span className="icon-bar" />
        <span className="icon-bar" />
        <span className="icon-bar" />
      </button>
      <a className="navbar-brand" href="#">
        Huha Test
      </a>
    </div>
    {/* Collect the nav links, forms, and other content for toggling */}
    <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">      
      <form className="navbar-form navbar-left" method="GET" role="search">
        <div className="form-group">
          <input type="text" name="q" className="form-control" placeholder="Search" />
        </div>
        <button type="submit" className="btn btn-default"><i className="glyphicon glyphicon-search" /></button>
      </form>
      <ul className="nav navbar-nav navbar-right">
        <li><a href="#" target="_blank">Xin chào, </a></li>
        <li className="dropdown ">
          <a href="#" className="dropdown-toggle" data-toggle="dropdown" role="button" aria-expanded="false">
            Đăng xuất
           </a>
        </li>
      </ul>
    </div>{/* /.navbar-collapse */}
  </div>{/* /.container-fluid */}
</nav>

    )
}
export default HeaderComponent;